	import { setStore, getStore, removeStore} from '../../utils/store.js'
	import tuiBadge from "@/components/badge/badge"
    import {
        mapState,
        mapMutations
    } from 'vuex'

    export default {
		components: {
			tuiBadge,
		},
		data() {
			return {
				toPayNum: 0,
				toReceivedNum: 0,
			}
		},
        computed: {
            ...mapState("common", ['hasLogin', 'userInfo', 'baseInfo'])
        },
        methods: {
            ...mapMutations('common', ['SET_USERIFNO']),
            bindLogin() {
                uni.navigateTo({
                    url: '../login/login',
                });
            },
            // bindLogout() {
            //     this.logout();
            //     /**
            //      * 如果需要强制登录跳转回登录页面
            //      */
            //     if (this.forcedLogin) {
            //         uni.reLaunch({
            //             url: '../login/login',
            //         });
            //     }
            // }
			navTo(url){
				if(!Boolean(this.hasLogin)){
					url = '/pages/login/login';
				}
				uni.navigateTo({  
					url
				})  
			},
			makePhoneCall(){
				uni.makePhoneCall({
				    phoneNumber: '4009006669'
				});
			},
			initPayNum(){
				this.$http.post('/mall/app/order/count', {
					...this.baseInfo,
					accountId: this.userInfo && this.userInfo.accountId,
					orderState: [0]
				})
				.then( res => {
					if(res.code == 0){
						this.toPayNum=res.result;
					}
				})
				.catch( err => {
					console.log(err);
				})
			},
			initToReceivedNum(){
				this.$http.post('/mall/app/order/count', {
					...this.baseInfo,
					accountId: this.userInfo && this.userInfo.accountId,
					orderState: [1, 2]
				})
				.then( res => {
					if(res.code == 0){
						this.toReceivedNum=res.result;
					}
				})
				.catch( err => {
					console.log(err);
				})
			}
        },
		onShow: function(){
			this.initPayNum();
			this.initToReceivedNum();
		}
    }
